<template>
  <video
    ref="base-player"
    :src="movie.localMovie.hostSrc"
    :type="
      movie.localMovie.movieType == 2
        ? 'video/x-matroska; codecs=\'theora, vorbis\''
        : 'video/mp4; codecs=\'av01.0.00M.08, opus\''
    "
    controls
    autoplay
    @onerror="failed(event)"
  ></video>
</template>
 
<script>
export default {
  data() {
    return {
      player: null,
    };
  },
  mounted() {
    this.player = this.$refs["base-player"];
    this.player.play();

    // this.playerOptions.sources[0].src = this.movie.localMovie.fullPath;
    // this.playerOptions.poster = this.movie.tmdbMovie.poster_path;
  },
  methods: {
    failed(e) {
      // video playback failed - show a message saying why
      switch (e.target.error.code) {
        case e.target.error.MEDIA_ERR_ABORTED:
          alert("You aborted the video playback.");
          break;
        case e.target.error.MEDIA_ERR_NETWORK:
          alert("A network error caused the video download to fail part-way.");
          break;
        case e.target.error.MEDIA_ERR_DECODE:
          alert(
            "The video playback was aborted due to a corruption problem or because the video used features your browser did not support."
          );
          break;
        case e.target.error.MEDIA_ERR_SRC_NOT_SUPPORTED:
          alert(
            "The video could not be loaded, either because the server or network failed or because the format is not supported."
          );
          break;
        default:
          alert("An unknown error occurred.");
          break;
      }
    },
  },
  props: ["movie"],
};
</script>


