<template>
  <div>
    <b-card class="col-12 flix-player-nav m-5 landing-info-header">
      <a class="h2" href="javascript:void(0)" @click="goBack">
        <b-icon-arrow-left-circle-fill
          variant="success"
        ></b-icon-arrow-left-circle-fill>
        {{ $t("Back") }}
      </a>
    </b-card>

    <base-player
      class="flix-player"
      v-if="movie && movie.localMovie.isSupported"
      :movie="movie"
    >
    </base-player>
  </div>
</template>

<script>
import VideoPlayerService from "@/api-services/video-player.service";
import Utils from "../../utils.js";
import BasePlayer from "@/components/Player/BasePlayer.vue";
import { EventBus } from "../../event-bus.js";

export default {
  name: "Video-player",
  components: {
    BasePlayer,
  },
  data() {
    return {
      movie: null,
    };
  },
  methods: {
    async initialize(id) {
      await VideoPlayerService.getMovieAsync(id)
        .then(async (response) => {
          if (response.status == 200) {
            this.movie = Object.assign(
              {},
              {
                localMovie: response.data.localMovie,
                tmdbMovie: response.data.tmdbMovie,
              }
            );
          }
        })
        .catch((err) => {
          Utils.showError(err);
        });
    },
    goBack() {
      if (window.history.length > 1) {
        this.$router.go(-1);
      } else {
        this.$router.push("/home");
      }
    },
  },
  async mounted() {
    EventBus.$emit("toggle-nav");
    await this.initialize(this.$route.params.id);
  },
  beforeDestroy() {
    EventBus.$emit("toggle-nav");
  },
};
</script>

<style>
.flix-player {
  object-fit: cover;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
}
.flix-player-nav {
  z-index: 2;
}
</style>


